<template>
  <div class="prop" v-show="level == 1">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Your KYC profile and ID will never be shared with Members</h3>
        <p
          class="form__input-info picture__subtitle"
        >Completing your KYC (Know your Customer) profile and providing a government ID helps us check you’re really you. It also helps us to keep SPLEET secure, fight against fraud and more.</p>
        <a href="#" class="personal__privacy">Check our privacy policy</a>

        <div class="memRequire">
          <div class="row mx-0 mb-3">
            <div class="col-lg-7 col-md-6 col-sm-12 px-0">
              <p class="form__section-title mb-1">Complete your Personal Information</p>
              <p class="form__input-info">Your first/last name, DOB, gender must match your ID</p>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <label for class="form__label">First Name</label>
              <input type="text" class="form__input" v-model="fname" />
            </div>
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pl-sm-4 pr-sm-0 form__row">
              <label for class="form__label">Last Name</label>
              <input type="text" class="form__input" v-model="lname" />
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-12 px-0">
              <label for class="form__label mb-2">Email</label>
              <p class="form__input-info mb-3">Your email has been confirmed and not editable</p>
              <div class="d-flex position-relative">
                <input type="text" disabled class="form__input verified" v-model="email" />
                <svgicon icon="verified" class="personal__verified"></svgicon>
              </div>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-12 px-0">
              <label for class="form__label mb-2">Phone Number</label>
              <p
                class="form__input-info mb-3"
              >Your phone number has been confirmed and not editable, to change it, go to your profile and add a New number</p>
              <div class="d-flex position-relative">
                <input type="text" disabled class="form__input verified" v-model="phoneNumber" />
                <svgicon icon="verified" class="personal__verified"></svgicon>
              </div>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <label for class="form__label mb-2">Gender</label>
              <div class="d-flex position-relative">
                <label class="form__check-wrap personal__gender mr-auto">
                  <label class="form__check-container">
                    <input
                      type="radio"
                      name="gender"
                      class="form__check"
                      value="female"
                      v-model.lazy="gender"
                    />
                    <span class="form__check-label">Female</span>
                    <span class="form__check-checkmark"></span>
                  </label>
                </label>
                <label class="form__check-wrap personal__gender">
                  <label class="form__check-container">
                    <input
                      type="radio"
                      name="gender"
                      class="form__check"
                      value="male"
                      v-model.lazy="gender"
                    />
                    <span class="form__check-label">Male</span>
                    <span class="form__check-checkmark"></span>
                  </label>
                </label>
              </div>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pl-sm-4 pr-sm-0 form__row">
              <label for class="form__label mb-2">Date of Birth</label>
              <div class="d-flex">
                <div class="form__select personal__day">
                  <multiselect v-model="DoBDay" :options="DoBDayOptions" placeholder="Day"></multiselect>
                </div>
                <div class="form__select personal__month">
                  <multiselect v-model="DoBMonth" :options="DoBMonthOptions" placeholder="Month"></multiselect>
                </div>
                <div class="form__select personal__year">
                  <multiselect v-model="DoBMonth" :options="DoBMonthOptions" placeholder="Year"></multiselect>
                </div>
              </div>
            </div>
          </div>

          <div class="row mx-0 form__section">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <label for class="form__label mb-2">Relationship Status</label>
              <div class="form__select">
                <multiselect v-model="relationshipStatus" :options="relationshipStatusOptions"></multiselect>
              </div>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pl-sm-4 pr-sm-0">
              <label for class="form__label mb-2">Number of Kids</label>
              <div class="form__select">
                <multiselect v-model="noKids" :options="noKidsOptions"></multiselect>
              </div>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-7 col-md-6 col-sm-12 px-0">
              <p class="form__section-title">Complete your Address</p>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0">
              <label for class="form__label mb-0">Full Address</label>
              <textarea
                name
                id
                class="form__textarea"
                placeholder="Enter full address, street name, apartment/house number, name."
                style="height:150px"
                v-model="personAddress"
              ></textarea>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0">
              <label for class="form__label mb-2">Landmark</label>
              <p class="form__input-info mb-3">Help us locate you faster</p>
              <input
                type="text"
                class="form__input"
                v-model="landmark"
                placeholder="After Ebeano supermarket"
              />
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0">
              <label for class="form__label">Country/Region</label>
              <div class="form__select">
                <multiselect v-model="country" :options="countryOptions"></multiselect>
              </div>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <div class="d-flex position-relative">
                <div class="personal__gender">
                  <label for class="form__label mb-2">State</label>
                  <div class="form__select">
                    <multiselect v-model="state" :options="stateOptions"></multiselect>
                  </div>
                </div>

                <div class="personal__gender ml-auto">
                  <label for class="form__label mb-2">City</label>
                  <div class="form__select">
                    <multiselect v-model="city" :options="cityOptions"></multiselect>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="step__next cta__btn" @click="incrementLevel">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";

export default {
  name: "PersonalInfo",
  components: {
    Multiselect
  },
  data() {
    return {
      fname: "",
      lname: "",
      email: "olasunkanmi@spleet.ng",
      phoneNumber: +2347033567533,
      gender: "",
      DoBDay: "",
      DoBDayOptions: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      DoBMonth: "",
      DoBMonthOptions: ["Jan", "Feb", "Mar"],
      relationshipStatus: "",
      relationshipStatusOptions: ["Single", "Married", "Divorced", "Widowed"],
      noKids: "",
      noKidsOptions: [1, 2, 3, "3+"],
      personAddress: "",
      landmark: "",
      country: "",
      countryOptions: ["Nigeria", "India"],
      state: "",
      stateOptions: ["Lagos", "Kaduna"],
      city: "",
      cityOptions: ["Ikeja", "Yaba"]
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    }
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



