<template>
  <modal name="priceAgreementModal">
    <div class="modal__wrap price__modal-wrap">
      <p class="priceAgree__modal-text">Disagree with Terms and Logout?</p>
    </div>

    <div class="modal__footer">
      <router-link tag="button" class="cta__btn mr-4" to="/">Disagree and Logout</router-link>
      <button type="button" class="declineBtn" @click="hide">Cancel</button>
    </div>
  </modal>
</template>

<script>
export default {
  name: "priceAgreementModal",
  data() {
    return {};
  },
  methods: {
    hide() {
      this.$modal.hide("priceAgreementModal");
    },
    beforeOpen(event) {
      this.item = event.params.item;
    }
  }
};
</script>


