<template>
  <div>
    <b-navbar class="navbar__header space__navbar" id="header-navbar" toggleable="lg">
      <b-navbar-brand>
        <router-link to="/">
          <div class="navbar__logo-wrapper">
            <img src="../../assets/images/logo.png" alt="spleet Logo" class="img-fluid" />
          </div>
        </router-link>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="ml-auto">
          <b-nav-item
            class="nav-item navbar__item active nav-link navbar__link"
            to="/breakdown"
          >Listing</b-nav-item>
          <b-nav-item
            v-show="!login"
            class="nav-item navbar__item nav-link navbar__link login"
            to="/register"
          >Login</b-nav-item>
          <b-nav-item
            v-show="login"
            class="nav-item navbar__item nav-link navbar__link login"
            @click="logOut"
          >Logout</b-nav-item>
          <button v-show="!login" class="navbar__getstarted-button">
            <router-link class="cta__btn" to="/register/signup">Get Started</router-link>
          </button>
          <button v-show="login" class="navbar__getstarted-button">
            <router-link class="cta__btn" to="/register/signup">Dashboard</router-link>
          </button>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>
<script>
import { createListing } from "../../../services/listing";

export default {
  name: "navbar",
  methods: {
    logOut() {
      localStorage.removeItem("user");
      this.$router.push("/register");
    },

    async listing() {
      await createListing();
    }
  },
  computed: {
    login() {
      return this.$store.state.login;
    }
  },
  created: function() {
    let user = JSON.parse(localStorage.getItem("user"));
    if (user) {
      return (this.$store.state.login = true);
    } else {
      return (this.$store.state.login = false);
    }
  }
};
// window.addEventListener("scroll", function(e) {
//   var headerNav = document.getElementById("header-navbar");
//   if (document.documentElement.scrollTop > 80) {
//     headerNav.classList.add("bg__scroll");
//   } else {
//     headerNav.classList.remove("bg__scroll");
//   }
// });
</script>


