<template>
  <div class="prop" v-show="level == 2">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Company/Employer’s Details</h3>
        <p class="form__input-info picture__subtitle">Enter your company or employers details</p>

        <div class="memRequire">
          <div class="row mx-0">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <label for class="form__label">Employment Status</label>
              <div class="form__select">
                <multiselect v-model="employmentStatus" :options="employmentStatusOptions"></multiselect>
              </div>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pl-sm-4 pr-sm-0 form__row">
              <label for class="form__label">Job Title</label>
              <input type="text" class="form__input" v-model="jobTitle" />
            </div>
          </div>
          <div class="row mx-0">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <label for class="form__label">Monthly Salary</label>
              <div class="form__select">
                <multiselect v-model="salary" :options="salaryOptions"></multiselect>
              </div>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pl-sm-4 pr-sm-0 form__section">
              <label for class="form__label">What is the Company/Business name?</label>
              <input type="text" class="form__input" v-model="businessName" />
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-7 col-md-6 col-sm-12 px-0">
              <p class="form__section-title">Business Partner or Manager’s details</p>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <label for class="form__label">Full Name</label>
              <input type="text" class="form__input" v-model="fname" />
            </div>
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pl-sm-4 pr-sm-0 form__row">
              <label for class="form__label">Mobile Number</label>
              <input type="text" class="form__input" v-model="phoneNumber" />
            </div>
          </div>

          <div class="row mx-0 form__section">
            <div class="col-lg-6 col-md-6 col-sm-12 px-0">
              <label for class="form__label">Email</label>
              <input type="text" class="form__input" v-model="email" />
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-7 col-md-6 col-sm-12 px-0">
              <p class="form__section-title">Complete Employer’s Address</p>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0">
              <label for class="form__label mb-0">Full Address</label>
              <textarea
                name
                id
                class="form__textarea"
                placeholder="Enter full address, street name, apartment/house number, name."
                style="height:150px"
                v-model="companyAddress"
              ></textarea>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0">
              <label for class="form__label mb-2">Landmark</label>
              <p class="form__input-info mb-3">Help us locate you faster</p>
              <input
                type="text"
                class="form__input"
                v-model="landmark"
                placeholder="After Ebeano supermarket"
              />
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0">
              <label for class="form__label">Country/Region</label>
              <div class="form__select">
                <multiselect v-model="country" :options="countryOptions"></multiselect>
              </div>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0 pr-0 pl-sm-0 form__row">
              <div class="d-flex position-relative">
                <div class="personal__gender">
                  <label for class="form__label mb-2">State</label>
                  <div class="form__select">
                    <multiselect v-model="state" :options="stateOptions"></multiselect>
                  </div>
                </div>

                <div class="personal__gender ml-auto">
                  <label for class="form__label mb-2">City</label>
                  <div class="form__select">
                    <multiselect v-model="city" :options="cityOptions"></multiselect>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="step__next cta__btn" @click="incrementLevel">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";

export default {
  name: "companyDetails",
  components: {
    Multiselect
  },
  data() {
    return {
      employmentStatus: "",
      employmentStatusOptions: [
        "Business Owner",
        "Employed",
        "Unemployed",
        "Intern/NYSC Corper",
        "Student"
      ],
      jobTitle: "",
      salary: "",
      salaryOptions: [
        "Under 100,000",
        "200,000 - 300,000",
        "400,000 - 500,000",
        "600,000 - 700,000",
        "800,000 - 900,000",
        "Above 1,000,000"
      ],
      businessName: "",
      email: "",
      fname: "",
      phoneNumber: "",
      companyAddress: "",
      landmark: "",
      country: "",
      countryOptions: ["Nigeria", "India"],
      state: "",
      stateOptions: ["Lagos", "Kaduna"],
      city: "",
      cityOptions: ["Ikeja", "Yaba"]
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    }
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



