<template>
  <div class="prop" v-show="level == 2">
    <form action @submit.prevent="handleSubmit">
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">What type of Subscription would you like Members to book?</h3>
        <p
          class="form__input-info picture__subtitle"
        >If you choose DLS, members will book available days instantly, alternatively, members can stay in your space for a minimum of 1 month.</p>

        <div class="memRequire">
          <div class="row mx-0 mb-2">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label">Have you rented out your place before?</label>
              <div class="mb-2" v-if="submitted && $v.rentBefore.$error">
                <div class="error" v-if="!$v.rentBefore.required">This field is required</div>
              </div>
              <label class="form__check-wrap">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="rentBefore"
                    class="form__check"
                    value="New to this"
                    v-model.lazy="rentBefore"
                  />
                  <span class="form__check-label">I am new to this</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>
            </div>
          </div>
          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label class="form__check-wrap">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="rentBefore"
                    class="form__check"
                    value="yes, I have"
                    v-model.lazy="rentBefore"
                  />
                  <span class="form__check-label">Yes, I have</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-7 col-md-6 col-sm-12 px-0">
              <p class="form__section-title">What type of subscription are you interested in?</p>
              <div class="mb-2" v-if="submitted && $v.subType.$error">
                <div class="error" v-if="!$v.subType.required">You must select either RLS or DLS</div>
              </div>
            </div>
          </div>

          <div class="row mx-0 mb-2">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label mb-1">Rolling Living Subscription (RLS)</label>
              <p class="form__input-info mb-3">Members can book for Monthly, Quarterly, Bi-annually</p>
              <label class="form__check-wrap">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="subType"
                    class="form__check"
                    value="monthly"
                    v-model.lazy="subType"
                  />
                  <span class="form__check-label">Monthly</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>
            </div>
          </div>
          <div class="row mx-0 mb-2">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label class="form__check-wrap" :class="{disable:!rlsValue}">
                <label class="form__check-container">
                  <input
                    type="checkbox"
                    name="RLS_quarterly"
                    class="form__check"
                    value="RLS_quarterly"
                    v-model.lazy="quarterly"
                    :disabled="!rlsValue"
                    :class="{disable:!rlsValue}"
                  />
                  <span class="form__check-label mb-0" :class="{disable:!rlsValue}">Quarterly</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label class="form__check-wrap" :class="{disable:!rlsValue}">
                <label class="form__check-container">
                  <input
                    type="checkbox"
                    name="RLS_biannually"
                    class="form__check"
                    value="RLS_biannually"
                    v-model.lazy="biannually"
                    :disabled="!rlsValue"
                    :class="{disable:!rlsValue}"
                  />
                  <span class="form__check-label mb-0" :class="{disable:!rlsValue}">Biannually</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>
            </div>
          </div>

          <div class="row mx-0 mb-2">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label mb-1">Daily Living Subscription (DLS)</label>
              <p class="form__input-info mb-3">Members can book for as little as 1 day to 30 days</p>
              <label class="form__check-wrap">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="subType"
                    class="form__check"
                    value="daily"
                    v-model.lazy="subType"
                  />
                  <span class="form__check-label">Daily</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="submit" class="step__next cta__btn">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { required, requiredIf } from "vuelidate/lib/validators";

export default {
  name: "subType",
  data() {
    return {
      rentBefore: "",
      submitted: false,
      rlsValue: false,
      dlsValue: false
    };
  },
  validations: {
    rentBefore: {
      required
    },
    subType: {
      required
    }
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    MemRequireShow() {
      this.$modal.show("MemRequireModal");
    },
    handleSubmit(e) {
      this.submitted = true;

      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.incrementLevel();
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    },
    subType: {
      get() {
        return this.$store.state.subType;
      },
      set(value) {
        this.$store.commit("updateSubType", value);
      }
    },
    quarterly: {
      get() {
        return this.$store.state.quarterly;
      },
      set(value) {
        this.$store.commit("updateQuarterly", value);
      }
    },
    biannually: {
      get() {
        return this.$store.state.biannually;
      },
      set(value) {
        this.$store.commit("updateBiannually", value);
      }
    }
  },
  watch: {
    subType(value) {
      if (value.includes("monthly")) {
        this.rlsValue = true;
        this.dlsValue = false;
      } else if (value.includes("daily")) {
        this.dlsValue = true;
        this.quarterly = false;
        this.biannually = false;
        this.rlsValue = false;
      }
    }
  }
};
</script>


