<template>
  <div v-show="level==3">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title">{{spaceType}}</h3>

        <div></div>
        <div id="bed-wrap">
          <Amenity v-for="(amenity, index) in featureName" :key="index" :title="featureName[index]"></Amenity>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button @click="incrementLevel" type="button" class="step__next cta__btn">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import Amenity from "./amenityType";

export default {
  name: "featureAmenity",
  components: {
    Multiselect,
    Amenity
  },
  data() {
    return {
      featureName: [
        {
          title: "What’s the Property Floor Plan?",
          amenities: [
            {
              name: "Foyer",
              icon: "bed"
            },
            {
              name: "Living Room",
              icon: "living-room"
            },
            {
              name: "Luggage Storage",
              icon: "luggage-storage"
            },
            {
              name: "Dining Room",
              icon: "dining-room"
            },
            {
              name: "Visitors Toilet",
              icon: "toilet"
            },
            {
              name: "Adjoining Living Room & Dining Room",
              icon: "dining-table"
            },
            {
              name: "Adjoining Kitchen & Living Room",
              icon: "kitchen"
            },
            {
              name: "Closed Kitchen",
              icon: "closed-kitchen"
            },
            {
              name: "Open Plan Kitchen",
              icon: "kitchen"
            },
            {
              name: "Kitchenette",
              icon: "kitchen"
            },
            {
              name: "Kitchen Storage",
              icon: "kitchen"
            },
            {
              name: "Balcony",
              icon: "balcony"
            }
          ]
        },
        {
          title: "What furnishing is in the Living Room and Dining Room?",
          amenities: [
            {
              name: "T.V",
              icon: "tv"
            },
            {
              name: "Fan",
              icon: "fan"
            },
            {
              name: "Sofa",
              icon: "sofa"
            },
            {
              name: "Couch",
              icon: "couch"
            },
            {
              name: "Side Table",
              icon: "side-table"
            },
            {
              name: "Center Table",
              icon: "center-table"
            },
            {
              name: "Dining Table & Chair",
              icon: "dining-table"
            }
          ]
        },
        {
          title: "What furnishing is in the Kitchen?",
          amenities: [
            {
              name: "Oven",
              icon: "oven"
            }
          ]
        },
        {
          title: "What amenities are included?",
          amenities: [
            {
              name: "15 Hours Power",
              icon: "power"
            },
            {
              name: "Supermarket",
              icon: "supermarket"
            }
          ]
        }
      ]
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    }
  },
  computed: {
    level() {
      return this.$store.getters.level;
    },
    spaceType() {
      return this.$store.state.selectedSpaceType;
    }
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



