<template>
  <modal :name="name">
    <div class="modal__heading">
      <div slot="top-right">
        <button type="button" class="modal__close-btn" @click="hide">
          <svgicon icon="cancel" class="modal__close"></svgicon>
        </button>
      </div>
      <div class="modal__title-wrap">
        <h3 class="modal__title">What is the price for {{rooms.name}}?</h3>
      </div>
    </div>
    <div class="modal__wrap price__modal-wrap">
      <div class="d-flex form__row">
        <div
          class="price__bdrm-img"
          :style="{backgroundImage: 'url('+'https://cdn.pixabay.com/photo/2018/05/28/22/11/message-in-a-bottle-3437294__340.jpg'+')'}"
        ></div>
        <div
          class="price__bdrm-img"
          :style="{backgroundImage: 'url('+require('../../assets/images/'+rooms.imageTwo+'')+')'}"
        ></div>
        <div
          class="price__bdrm-img"
          :style="{backgroundImage: 'url('+require('../../assets/images/'+rooms.imageThree+'')+')'}"
        ></div>
      </div>

      <div class="row mx-0">
        <div class="w-75 price__grp">
          <div v-show="rlsValue">
            <div class="form-group mb-2">
              <label for class="form__label">Rolling Living Subscription (RLS)</label>
              <label for class="form__label">Monthly</label>
              <input
                v-model.number="rooms.monthlyPrice"
                name="RLS_monthly"
                type="number"
                class="form__input"
                @blur="pricingCal"
              />
            </div>

            <div class="form-group mb-2" v-show="quarterly">
              <label for class="form__label disable">Quarterly</label>
              <input
                disabled
                name="RLS_quarterly"
                type="number"
                class="form__input disable"
                :value="rooms.quarterlyPrice"
              />
            </div>

            <div class="form-group" v-show="biannually">
              <label for class="form__label disable">Biannually</label>
              <input
                disabled
                :value="rooms.biannuallyPrice"
                name="RLS_biannually"
                type="number"
                class="form__input disable"
              />
            </div>
          </div>

          <div v-show="dlsValue">
            <div class="form-group mb-2">
              <label for class="form__label">Daily Living Subscription (DLS) - Daily</label>
              <input v-model.number="rooms.price" name="daily" type="number" class="form__input" />
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="modal__footer">
      <button type="button" class="cta__btn" @click="hide">Save</button>
    </div>
  </modal>
</template>

<script>
export default {
  name: "PriceModal",
  props: {
    rooms: Object,
    name: String
  },
  data() {
    return {
      rlsValue: false,
      dlsValue: false
    };
  },
  methods: {
    hide() {
      this.$modal.hide(this.name);
    },
    beforeOpen(event) {
      this.item = event.params.item;
    },
    quarterlyCal() {
      this.rooms.quarterlyPrice = this.rooms.monthlyPrice * 3;
    },
    biannuallyCal() {
      this.rooms.biannuallyPrice = this.rooms.monthlyPrice * 6;
    },
    cumulatePrice() {
      this.quarterlyCal();
      this.biannuallyCal();
    },
    subTypeCheck() {
      if (this.subType.includes("monthly")) {
        this.rlsValue = true;
        this.dlsValue = false;
      } else if (this.subType.includes("daily")) {
        this.dlsValue = true;
        this.rlsValue = false;
      }
    }
  },
  computed: {
    pricingCal() {
      return this.cumulatePrice();
    },
    quarterly() {
      return this.$store.state.quarterly;
    },
    biannually() {
      return this.$store.state.biannually;
    },
    subType() {
      return this.$store.state.subType;
    }
  },
  updated() {
    this.subTypeCheck();
  },
  mounted() {
    this.subTypeCheck();
  }
};
</script>


