<template>
  <div class="prop" v-if="level == 3 && rlsValue">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Price your space</h3>

        <div class="memRequire">
          <div class="row mx-0 form__row">
            <div class="col-lg-7 col-md-6 col-sm-12 px-0">
              <p
                class="form__section-title"
                v-show="entireSpace"
              >What is the price of the Entire Space</p>
              <p
                class="form__section-title"
                v-show="coSharedSpace"
              >What is the price of the Co-share Space</p>
              <p
                class="form__input-info"
              >Rent covers the fixed price for the property and service charge</p>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label">Currency</label>
              <div class="form__select">
                <multiselect v-model="currency" :options="currencyOptions"></multiselect>
              </div>
            </div>
          </div>

          <div v-show="entireSpace">
            <div class="row mx-0">
              <div class="col-lg-5 col-md-6 col-sm-12 px-0">
                <div class="form-group mb-2">
                  <label for class="form__label">Rolling Living Subscription (RLS) - Monthly</label>
                  <input
                    v-model.number="pricing[0].RLS_monthly"
                    name="RLS_monthly"
                    type="number"
                    class="form__input"
                    @blur="pricingCal"
                  />
                </div>

                <div class="form-group mb-2" v-show="quarterly">
                  <label for class="form__label disable">Quarterly</label>
                  <input
                    disabled
                    name="RLS_quarterly"
                    type="number"
                    class="form__input disable"
                    :value="pricing[0].RLS_quarterly"
                  />
                </div>

                <div class="form-group" v-show="biannually">
                  <label for class="form__label disable">Biannually</label>
                  <input
                    disabled
                    :value="pricing[0].RLS_biannually"
                    name="RLS_biannually"
                    type="number"
                    class="form__input disable"
                  />
                </div>

                <hr class="form__divider" />
              </div>
            </div>
          </div>

          <div v-show="coSharedSpace" class="price__bdrm form__row">
            <div class="price__bdrm-wrap">
              <div class="bdrm__preview-table-wrap">
                <div class="bdrm__preview-heading">
                  <div class="bdrm__preview-cell bdrm__preview-name">
                    <p class="bdrm__preview-heading-text">Bedroom</p>
                  </div>
                  <div class="bdrm__preview-cell bdrm__preview-bathroom">
                    <p class="bdrm__preview-heading-text">Bathroom type</p>
                  </div>
                  <div class="bdrm__preview-cell bdrm__preview-master">
                    <p class="bdrm__preview-heading-text">Master bedroom</p>
                  </div>
                  <div class="bdrm__preview-cell bdrm__preview-edit">
                    <p class="bdrm__preview-heading-text">Price per Month</p>
                  </div>
                  <div class="bdrm__preview-cell bdrm__preview-delete">
                    <p class="bdrm__preview-heading-text"></p>
                  </div>
                </div>
              </div>

              <div class="bdrm__preview-body" v-for="(room, index) in rooms" :key="index">
                <div class="bdrm__preview-cell bdrm__preview-name">
                  <p class="bdrm__preview-title">{{room.name}}</p>
                </div>
                <div class="bdrm__preview-cell bdrm__preview-bathroom">
                  <p class="bdrm__preview-title">{{room.bathroomType}}</p>
                </div>
                <div class="bdrm__preview-cell bdrm__preview-master">
                  <p class="bdrm__preview-title">{{room.masterBed ? "Yes":"No"}}</p>
                </div>
                <div class="bdrm__preview-cell bdrm__preview-edit">
                  <p class="bdrm__preview-title">{{room.monthlyPrice}}</p>
                </div>
                <div class="bdrm__preview-cell bdrm__preview-delete">
                  <span
                    class="bdrm__preview-title price__add"
                    @click="priceModalShow(room.name)"
                  >{{changePriceName(room.monthlyPrice)}}</span>
                </div>
                <div class="price__modal">
                  <PriceModal :rooms="room" :name="room.name"></PriceModal>
                </div>
              </div>
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0">
              <label
                for
                class="form__label"
              >Would you like SPLEET to manage cleaning services for your space?</label>
              <p
                class="form__input-info"
                style="margin-bottom:12px"
              >We have highly recommended third party who offer exceptional cleaning</p>

              <label class="form__check-wrap mb-3">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="manageCLeaning"
                    class="form__check"
                    value="Spleet"
                    v-model.lazy="manageCLeaning"
                  />
                  <span class="form__check-label">Yes, SPLEET should manage my cleaning services</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>

              <label class="form__check-wrap form__row">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="manageCLeaning"
                    class="form__check"
                    value="Home Owner"
                    v-model.lazy="manageCLeaning"
                  />
                  <span class="form__check-label">No, I will provide cleaning services</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>

              <div v-show="homeOwnerCleans">
                <div class="form__row">
                  <label
                    for
                    class="form__label"
                  >How often do you want to offer Members Cleaning services?</label>
                  <p
                    class="form__input-info"
                    style="margin-bottom:12px"
                  >It is mandatory to offer cleaning at least once a week for the communal space and private spaces for RLS</p>
                  <div class="form__select">
                    <multiselect v-model="howOftenCleaning" :options="howOftenCleaningOptions"></multiselect>
                  </div>
                </div>

                <div class="form__row">
                  <label for class="form__label">What is the cost of cleaning for your space?</label>

                  <input
                    v-model.number="cleaningCost"
                    name="cleaningCost"
                    type="number"
                    class="form__input"
                  />
                </div>
              </div>

              <hr class="form__divider" />
            </div>
          </div>

          <div class="row mx-0">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0">
              <label
                for
                class="form__label"
              >Would you like SPLEET to manage maintenance for your space?</label>
              <p
                class="form__input-info"
                style="margin-bottom:12px"
              >We have highly recommended third party who offer exceptional maintenance</p>

              <label class="form__check-wrap mb-3">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="manageMaintenance"
                    class="form__check"
                    value="Spleet"
                    v-model.lazy="manageMaintenance"
                  />
                  <span class="form__check-label">Yes, SPLEET should manage my maintenance</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>

              <label class="form__check-wrap form__row">
                <label class="form__check-container">
                  <input
                    type="radio"
                    name="manageMaintenance"
                    class="form__check"
                    value="Home Owner"
                    v-model.lazy="manageMaintenance"
                  />
                  <span class="form__check-label">No, I will provide the maintenance</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>

              <div v-show="homeOwnerMaintenance">
                <label for class="form__label">What is the cost of maintenance for your space?</label>
                <input
                  v-model.number="maintenanceCost"
                  name="maintenanceCost"
                  type="number"
                  class="form__input"
                />
              </div>

              <hr class="form__divider" />
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label
                for
                class="form__label"
              >What is the cost of Gas and Power Supply for your space?</label>
              <p
                class="form__input-info"
                style="margin-bottom:12px"
              >What is the cost of power supply such as fuel or diesel for the generator and gas for the gas cooker</p>
              <input v-model.number="gasCost" name="gasCost" type="number" class="form__input" />
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="step__next cta__btn" @click="nextButton">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import PriceModal from "./pricingModal";

export default {
  name: "pricingMonthly",
  components: {
    Multiselect,
    PriceModal
  },
  data() {
    return {
      rooms: [
        {
          name: "bedroom 1",
          id: "",
          bathroomType: "Ensuite bathroom",
          masterBed: true,
          monthlyPrice: 0.0,
          quarterlyPrice: "",
          biannuallyPrice: "",
          imageOne: "bedroom_slide.jpg",
          imageTwo: "cardogan.jpg",
          imageThree: "space4.jpg"
        },
        {
          name: "bedroom 2",
          id: "",
          bathroomType: "Shared bathroom",
          masterBed: false,
          monthlyPrice: 0.0,
          quarterlyPrice: "",
          biannuallyPrice: "",
          imageOne: "space4.jpg",
          imageTwo: "bedroom_slide.jpg",
          imageThree: "cardogan.jpg"
        },
        {
          name: "bedroom 3",
          id: "",
          bathroomType: "Adjoining bathroom",
          masterBed: false,
          monthlyPrice: 0.0,
          quarterlyPrice: "",
          biannuallyPrice: "",
          imageOne: "cardogan.jpg",
          imageTwo: "space4.jpg",
          imageThree: "bedroom_slide.jpg"
        }
      ],
      rlsValue: false,
      dlsValue: false,
      currency: "",
      currencyOptions: ["Nigeria, Naira", "Ghana, Cedis"],
      pricing: [
        {
          RLS_monthly: "",
          RLS_quarterly: "",
          RLS_biannually: ""
        }
      ],
      manageCLeaning: "",
      howOftenCleaning: "",
      howOftenCleaningOptions: [
        "Once per week",
        "Twice per week",
        "Thrice per week"
      ],
      cleaningCost: "",
      homeOwnerCleans: false,
      homeOwnerMaintenance: false,
      manageMaintenance: "",
      maintenanceCost: "",
      gasCost: ""
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    priceModalShow(index) {
      this.$modal.show(index);
    },
    handleSubmit(e) {
      this.submitted = true;

      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.incrementLevel();
    },
    spaceTypeCheck() {
      if (this.spaceType.includes("Studio")) {
        this.$store.state.entireSpace = true;
        this.$store.state.coSharedSpace = false;
      } else if (this.spaceType.includes("Entire")) {
        this.$store.state.entireSpace = true;
        this.$store.state.coSharedSpace = false;
      } else if (this.spaceType.includes("Co-sharing")) {
        this.$store.state.entireSpace = false;
        this.$store.state.coSharedSpace = true;
      }
    },
    subTypeCheck() {
      this.spaceTypeCheck();
      if (this.subType.includes("monthly")) {
        this.rlsValue = true;
        this.dlsValue = false;
      } else if (this.subType.includes("daily")) {
        this.dlsValue = true;
        this.rlsValue = false;
      }
    },
    quarterlyCal() {
      this.pricing[0].RLS_quarterly = this.pricing[0].RLS_monthly * 3;
    },
    biannuallyCal() {
      this.pricing[0].RLS_biannually = this.pricing[0].RLS_monthly * 6;
    },
    cumulatePrice() {
      this.quarterlyCal();
      this.biannuallyCal();
    },
    changePriceName(value) {
      if (value < 1) {
        return "Add Price";
      } else {
        return "Change Price";
      }
    },
    nextButton() {
      if (this.quarterly || this.biannually) {
        this.incrementLevel();
      } else {
        this.$store.state.level = 6;
      }
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    },
    spaceType() {
      return this.$store.state.selectedSpaceType;
    },
    subType() {
      return this.$store.state.subType;
    },
    entireSpace() {
      return this.$store.state.entireSpace;
    },
    coSharedSpace() {
      return this.$store.state.coSharedSpace;
    },
    quarterly() {
      return this.$store.state.quarterly;
    },
    biannually() {
      return this.$store.state.biannually;
    },
    pricingCal() {
      return this.cumulatePrice();
    }
  },
  updated() {
    this.subTypeCheck();
  },
  mounted() {
    this.subTypeCheck();
  },
  watch: {
    manageCLeaning() {
      if (this.manageCLeaning.includes("Home Owner")) {
        this.homeOwnerCleans = true;
      } else {
        this.homeOwnerCleans = false;
      }
    },

    manageMaintenance() {
      if (this.manageMaintenance.includes("Home Owner")) {
        this.homeOwnerMaintenance = true;
      } else {
        this.homeOwnerMaintenance = false;
      }
    }
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



