<template>
  <div class="prop" v-if="level == 5 && dlsValue">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Something special for your Members</h3>
        <p
          class="form__input-info picture__subtitle"
        >Encourage home seekers to book longer stays by offering a discount.</p>

        <div class="memRequire">
          <label for class="form__label form__row">Daily Living Subscription (DLS)</label>

          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <div class="form__row">
                <label for class="form__label">7-14 days booking (%)</label>
                <div class="form__select">
                  <multiselect v-model="sevenToFourteenDiscount" :options="discountOptions"></multiselect>
                </div>
              </div>

              <div class="form__row">
                <label for class="form__label">14-21 days booking (%)</label>
                <div class="form__select">
                  <multiselect v-model="fourteenToTwentyOneDiscount" :options="discountOptions"></multiselect>
                </div>
              </div>

              <div class="form__row">
                <label for class="form__label">21-above days booking (%)</label>
                <div class="form__select">
                  <multiselect v-model="TwentyOneAboveDiscount" :options="discountOptions"></multiselect>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="step__next cta__btn" @click="nextButton">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";

export default {
  name: "dlsDiscount",
  components: {
    Multiselect
  },

  data() {
    return {
      rlsValue: false,
      dlsValue: false,
      sevenToFourteenDiscount: "",
      discountOptions: [0, 3, 5, 7.5, 10],
      fourteenToTwentyOneDiscount: "",
      TwentyOneAboveDiscount: ""
    };
  },

  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    subTypeCheck() {
      if (this.subType.includes("monthly")) {
        this.rlsValue = true;
        this.dlsValue = false;
      } else if (this.subType.includes("daily")) {
        this.dlsValue = true;
        this.rlsValue = false;
      }
    },
    nextButton() {
      return (this.$store.state.level = 6);
    }
  },

  computed: {
    level() {
      return this.$store.state.level;
    },
    subType() {
      return this.$store.state.subType;
    }
  },
  updated() {
    this.subTypeCheck();
  },
  mounted() {
    this.subTypeCheck();
  }
};
</script>


