<template>
  <div v-show="level==2">
    <div class="bdrm__wrap">
      <div class="d-flex flex-wrap">
        <div class="bdrm__form">
          <h3 class="listing__title">{{spaceType}}</h3>
          <div v-if="!bedroomForm">
            <!-- <div class="bdrm__count-wrap">
                <button
                  type="button"
                  class="bdrm__count-btn"
                  :class="{disable:bedroomCount <= 1}"
                  @click="bedroomCountDecrement"
                  :disabled="bedroomCount<=1 ? true:false"
                >-</button>
                <span class="bdrm__count-view">{{ bedroomCount }}</span>
                <button
                  type="button"
                  class="bdrm__count-btn"
                  :class="{disable:onlyOneBed}"
                  @click="bedroomCountIncrement"
                  :disabled="onlyOneBed ? true:false"
                >+</button>
            </div>-->
            <button
              class="alt__btn d-block"
              type="button"
              @click="showForm"
              :class="{disable:studio}"
              :disabled="studio ? true:false"
            >{{addRoomBtn ? `Add Bedroom` : `Add more room`}}</button>
          </div>
          <div id="bed-wrap">
            <!-- <BedroomEdit v-for="(bedroomCounts, index) in bedroomCount" :key="index"></BedroomEdit> -->
            <form action @submit.prevent="saveRoom" v-if="bedroomForm">
              <h3 class="form__label mb-5">Fill bedroom details</h3>
              <div class="bathroom__wrap">
                <div class="bdrm__edit-master mb-4">
                  <label class="form__check-container">
                    <input
                      type="checkbox"
                      name="masterBedroom"
                      class="form__check"
                      v-model="bedroom.masterBed"
                    />
                    <span class="form__check-label">Is this a master bedroom?</span>
                    <span class="form__check-checkmark"></span>
                  </label>
                </div>

                <div class="bdrm__bathroom">
                  <label for class="form__label mb-2">Is there an Ensuite or Adjoining Bathroom?</label>
                  <div class="mb-2" v-if="submitted && $v.bedroom.bathroomType.$error">
                    <div
                      class="error"
                      v-if="!$v.bedroom.bathroomType.required"
                    >Bathroom type is required</div>
                  </div>
                  <div class="row mx-0 form__row">
                    <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
                      <label class="form__check-wrap">
                        <label class="form__check-container">
                          <input
                            type="radio"
                            name="bathroomType"
                            class="form__check"
                            value="Ensuite bathroom"
                            v-model="bedroom.bathroomType"
                          />
                          <span class="form__check-label">Ensuite bathroom</span>
                          <span class="form__check-checkmark"></span>
                        </label>
                      </label>
                    </div>
                    <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4">
                      <p class="form__input-info">Private bathtub or shower and toilet</p>
                    </div>
                  </div>
                  <div class="row mx-0 form__row">
                    <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
                      <label class="form__check-wrap">
                        <label class="form__check-container">
                          <input
                            type="radio"
                            name="bathroomType"
                            class="form__check"
                            value="Adjoining bathroom"
                            v-model="bedroom.bathroomType"
                          />
                          <span class="form__check-label">Adjoining bathroom</span>
                          <span class="form__check-checkmark"></span>
                        </label>
                      </label>
                    </div>
                    <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4">
                      <p
                        class="form__input-info"
                      >Bathtub or shower and toilet, shared between 2 bedrooms</p>
                    </div>
                  </div>
                  <div class="row mx-0 form__row">
                    <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
                      <label class="form__check-wrap">
                        <label class="form__check-container">
                          <input
                            type="radio"
                            name="bathroomType"
                            class="form__check"
                            value="Shared bathroom"
                            v-model="bedroom.bathroomType"
                          />
                          <span class="form__check-label">Shared bathroom</span>
                          <span class="form__check-checkmark"></span>
                        </label>
                      </label>
                    </div>
                    <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4">
                      <p
                        class="form__input-info"
                      >One Bathtub or shower and toilet shared by everyone in the property</p>
                    </div>
                  </div>
                </div>

                <div class="bdrm__furnish">
                  <div v-if="bedroomAmenities.length < 1 && amenitiesError">
                    <p class="error">You haven't selected any amenity</p>
                  </div>

                  <label
                    for
                    class="form__label"
                  >What type of furnishing is in Bedroom {{bedroomCount}}?</label>
                  <span class="form__add" @click="bathFurnShow">+Add</span>
                  <Modal
                    :numBedroom="numBedroom"
                    @updateBedAmenities="updateBedroom"
                    :bedroomAmenities="bedroomAmenities"
                    :amenities="amenities"
                    :modalTitle="newModalTitle"
                    :modalSubtitle="newModalSubtitle"
                    :name="bdrmAmenityName"
                  />
                  <div class="bdrm__furnish-wrap">
                    <div
                      class="bdrm__furnish-indvdul"
                      v-for="(bedroomAmenity, id) in bedroomAmenities"
                      :key="id"
                    >
                      <span>{{ bedroomAmenity }}</span>
                      <svgicon
                        icon="cancel"
                        class="bdrm__furnish-indvdul-icon"
                        @click="deleteAmenityOn(id)"
                      ></svgicon>
                    </div>
                  </div>
                </div>

                <div class="bdrm__img">
                  <div v-if="uploadedFiles.length < 1 && imgError">
                    <p class="error">You haven't add any image for this bedroom</p>
                  </div>

                  <div class="d-flex">
                    <p class="form__label mr-auto mb-1">Upload Bedroom images</p>
                    <form enctype="multipart/form-data" novalidate>
                      <label class="form__add d-block ml-auto mb-0">
                        <span v-if="!isSuccess || uploadedFiles.length <= 0">+Add</span>
                        <input
                          type="file"
                          multiple
                          :name="uploadFieldName"
                          @change="filesChange($event.target.name, $event.target.files); fileCount = $event.target.files.length"
                          accept="image/*"
                          class="input-file picture__more"
                        />
                      </label>
                      <p v-if="isSaving">Uploading {{ fileCount }} images...</p>
                    </form>
                  </div>

                  <!--SUCCESS-->
                  <div v-if="isSuccess" class="picture__upload-success">
                    <form
                      v-show="uploadedFiles.length >= 1"
                      enctype="multipart/form-data"
                      novalidate
                    >
                      <label class="form__add d-block mb-0 ml-auto">
                        <span>+Add more</span>
                        <input
                          type="file"
                          multiple
                          :name="uploadFieldName"
                          @change="addChange($event.target.name, $event.target.files); fileCount = $event.target.files.length"
                          accept="image/*"
                          class="input-file picture__more"
                        />
                      </label>
                    </form>

                    <div class="row mx-0">
                      <div
                        class="col-lg-3 col-sm-4 col-6 mb-3"
                        v-for="(item, index) in uploadedFiles"
                        :key="index"
                      >
                        <div class="picture__upload-list">
                          <div
                            class="bdrm__img-list"
                            :style="{backgroundImage: 'url('+require('../../assets/images/'+item.fileName+'')+')'}"
                            :imageName="item.fileName"
                            :id="index"
                          >
                            <div class="bdrm__img-delete" @click="deleteImage(index)">
                              <svgicon icon="cancel" class="bdrm__img-delete-icon"></svgicon>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>

                    <!--FAILED-->
                    <div v-if="isFailed">
                      <h2>Uploaded failed.</h2>
                      <p>
                        <a href="javascript:void(0)" @click="reset()">Try again</a>
                      </p>
                      <pre>{{ uploadError }}</pre>
                    </div>
                  </div>
                </div>

                <button type="submit" class="cta__btn">{{ bedroom.id == "" ? `Save` : `Update` }}</button>
              </div>
            </form>
          </div>
        </div>

        <div class="bdrm__preview">
          <!-- v-for="(bedroomCounts, index) in bedroomCount" :key="index" -->
          <h3 class="listing__title">Rooms Added</h3>

          <div class="bdrm__preview-table-wrap">
            <div class="bdrm__preview-heading">
              <div class="bdrm__preview-cell bdrm__preview-name">
                <p class="bdrm__preview-heading-text">Bedroom</p>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-bathroom">
                <p class="bdrm__preview-heading-text">Bathroom type</p>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-master">
                <p class="bdrm__preview-heading-text">Master bedroom</p>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-edit">
                <p class="bdrm__preview-heading-text">Edit</p>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-delete">
                <p class="bdrm__preview-heading-text">Delete</p>
              </div>
            </div>
            <p class="error" v-show="empty">You haven't added any bedroom</p>
          </div>

          <div class="bdrm__preview-table-wrap">
            <div class="bdrm__preview-body" v-for="(room, index) in rooms" :key="index">
              <div class="bdrm__preview-cell bdrm__preview-name">
                <p class="bdrm__preview-title">Bedroom</p>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-bathroom">
                <p class="bdrm__preview-title">{{room.bathroomType}}</p>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-master">
                <p class="bdrm__preview-title">{{room.masterBed ? "Yes":"No"}}</p>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-edit">
                <a href="#" class="bdrm__preview-title">
                  <svgicon icon="edit" class="bdrm__preview-icon"></svgicon>
                </a>
              </div>
              <div class="bdrm__preview-cell bdrm__preview-delete">
                <a href="#" class="bdrm__preview-title">
                  <svgicon icon="delete" class="bdrm__preview-icon"></svgicon>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="step">
      <div class="step__wrap">
        <div class="step__prev">
          <button type="button" v-show="level > 1" @click="decrementLevel">
            <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
            <span>Back</span>
          </button>
        </div>
        <button type="button" @click="nextTab" class="step__next cta__btn">Continue</button>
      </div>
    </div>
  </div>
</template>
<script>
import Multiselect from "vue-multiselect";
// import BedroomEdit from "./bedroomEdit";
import { upload } from "./pictureService.js";
import Modal from "./modal";
import { required } from "vuelidate/lib/validators";

const STATUS_INITIAL = 0,
  STATUS_SAVING = 1,
  STATUS_SUCCESS = 2,
  STATUS_FAILED = 3;

export default {
  name: "bedroom",
  components: {
    Multiselect,
    Modal
  },
  data() {
    return {
      rooms: [],
      bedroom: {
        name: "bedroom",
        id: "",
        bathroomType: "",
        masterBed: false,
        amenities: "",
        images: ""
      },
      bedroomAmenities: [],
      isHidden: false,
      numBedroom: this.$vnode.key,
      modalTitle: "What type of furnishing is in Bedroom ",
      modalSubtitle: "Select furnishings you have in bedroom ",
      name: "bedroom",
      amenities: [
        {
          name: "Bed",
          icon: "bed"
        },
        {
          name: "Air Conditioner",
          icon: "air-conditioner"
        },
        {
          name: "Double Bed",
          icon: "double-bed"
        },
        {
          name: "Hanger",
          icon: "hanger"
        },
        {
          name: "Lock on bedroom door",
          icon: "lock-on-bedroom-door"
        },
        {
          name: "Mirror",
          icon: "mirror"
        },
        {
          name: "Pillow",
          icon: "pillow"
        },
        {
          name: "Side Drawer",
          icon: "side-drawe"
        },
        {
          name: "Wardrobe",
          icon: "wardrobe"
        },
        {
          name: "Bathtub",
          icon: "bathtub"
        },
        {
          name: "Bedsheet",
          icon: "bed-sheet"
        },
        {
          name: "Balcony",
          icon: "balcony"
        }
      ],
      uploadedFiles: [],
      uploadError: null,
      currentStatus: null,
      uploadFieldName: "photos",
      selectedType: [],
      imageSelect: [],
      formData: "",
      bedroomForm: false,
      submitted: true,
      imgError: false,
      amenitiesError: false,
      addRoomBtn: true,
      studio: false,
      empty: false
    };
  },
  validations: {
    bedroom: {
      bathroomType: {
        required
      }
    }
  },
  methods: {
    nextTab() {
      if (this.rooms.length == 0) {
        return (this.empty = true);
      } else {
        this.empty = false;
        this.incrementLevel();
      }
    },
    handleSubmit(e) {
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }

      this.bedroomForm = false;
      this.rooms.push(this.bedroom);
      this.addRoomBtn = false;
      this.resetForm();
      alert("passed");
    },
    resetForm() {
      this.bedroom = {
        id: "",
        bathroomType: "",
        masterBed: "",
        amenities: "",
        images: ""
      };
      this.uploadedFiles = [];
      this.bedroomAmenities = [];
    },
    showForm() {
      this.bedroomForm = true;
      this.bedroomCountIncrement();
    },
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    bedroomCountIncrement() {
      this.$store.dispatch("bedroomIncrementAction", 1);
    },
    bedroomCountDecrement() {
      this.$store.dispatch("bedroomDecrementAction", 1);
    },
    bathFurnShow() {
      this.$modal.show(this.bdrmAmenityName);
    },
    updateBedroom(value) {
      this.bedroomAmenities = value;
    },
    deleteAmenityOn(id) {
      this.bedroomAmenities.splice(id, 1);
    },
    hide() {
      this.$modal.hide(this.name);
    },
    saveRoom() {
      if (this.bedroomAmenities.length > 0) {
        if (this.uploadedFiles.length > 0) {
          this.bedroom.amenities = this.bedroomAmenities;
          this.bedroom.images = this.uploadedFiles;
          this.empty = false;
          if (this.onlyOneBed == true) {
            this.studio = true;
          }
          this.handleSubmit();
        } else {
          this.imgError = true;
        }
      } else {
        this.amenitiesError = true;
      }
    },

    reset() {
      // reset form to initial state
      this.currentStatus = STATUS_INITIAL;
      this.uploadedFiles = [];
      this.uploadError = null;
    },
    onSelect(selectedOption, id) {
      const imageValue = {
        uploadIndex: id,
        imageName: this.uploadedFiles[id].fileName,
        imageType: selectedOption
      };
      //this.imageSelect.push(imageValue);
      var objIndex = this.imageSelect.filter(obj => obj.uploadIndex == id);

      if (objIndex.length) {
        this.imageSelect.splice(id, 1, imageValue);
        // console.log(this.imageSelect);
      } else {
        return this.imageSelect.push(imageValue);
      }
    },
    save(formData) {
      // upload data to the server
      this.currentStatus = STATUS_SAVING;

      upload(formData)
        .then(x => {
          this.uploadedFiles = [].concat(x);
          this.currentStatus = STATUS_SUCCESS;
        })
        .catch(err => {
          this.uploadError = err.response;
          this.currentStatus = STATUS_FAILED;
        });
    },
    filesChange(fieldName, fileList) {
      // handle file changes
      this.formData = new FormData();

      if (!fileList.length) return;

      // append the files to FormData
      Array.from(Array(fileList.length).keys()).map(x => {
        this.formData.append(fieldName, fileList[x], fileList[x].name);
      });

      // save it
      //   for (var soemth in this.formData.get(fieldName)) {
      //     console.log(soemth);
      //   }
      this.save(this.formData);
    },
    addChange(fieldName, fileList) {
      // handle file changes

      if (fileList.length) {
        // append the files to FormData
        Array.from(Array(fileList.length).keys()).map(x => {
          this.formData.append(fieldName, fileList[x], fileList[x].name);
        });

        // save it
        this.save(this.formData);
      }
    },
    deleteImage(id) {
      this.uploadedFiles.splice(id, 1);
    }
  },
  computed: {
    level() {
      return this.$store.getters.level;
    },
    bedroomCount() {
      return this.$store.getters.bedroomCount;
    },
    spaceType() {
      return this.$store.state.selectedSpaceType;
    },
    onlyOneBed() {
      return this.$store.state.onlyOneBed;
    },
    bdrmAmenityName() {
      return this.name + this.numBedroom;
    },
    newModalTitle() {
      return this.modalTitle + Number(this.numBedroom + 1);
    },
    newModalSubtitle() {
      return this.modalSubtitle + Number(this.numBedroom + 1);
    },

    isInitial() {
      return this.currentStatus === STATUS_INITIAL;
    },
    isSaving() {
      return this.currentStatus === STATUS_SAVING;
    },
    isSuccess() {
      return this.currentStatus === STATUS_SUCCESS;
    },
    isFailed() {
      return this.currentStatus === STATUS_FAILED;
    }
  },
  mounted() {
    this.reset();
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>


