<template>
  <div class="prop" v-show="level == 5">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Enter your Payment Details</h3>
        <p
          class="form__input-info picture__subtitle"
        >Add your Bank details so SPLEET can pay you once a successful subscription has been created</p>

        <div class="memRequire">
          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0">
              <label for class="form__label">Name of Bank</label>
              <div class="form__select">
                <multiselect v-model="bankName" :options="bankNameOptions"></multiselect>
              </div>
            </div>
          </div>
          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0">
              <label for class="form__label">Account Number</label>
              <input type="number" class="form__input" v-model.number="accountNumber" />
            </div>
          </div>
          <div class="row mx-0 form__row">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0">
              <label for class="form__label">BVN</label>
              <input type="number" class="form__input" v-model.number="bvn" />
            </div>
          </div>

          <div class="row mx-0 form__section">
            <div class="col-lg-6 col-md-6 col-sm-6 col-12 px-0">
              <label for class="form__label">Account Name</label>
              <input type="text" class="form__input" disabled v-model="accountName" />
            </div>
          </div>
          <button type="button" class="cta__btn">Verify</button>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <router-link to="/breakdown" tag="button" class="step__next cta__btn">Continue</router-link>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";

export default {
  name: "companyDetails",
  components: {
    Multiselect
  },
  data() {
    return {
      bankName: "",
      bankNameOptions: ["UBA", "First Bank", "Access Bank", "Fidelity"],
      accountNumber: "",
      bvn: "",
      accountName: ""
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    }
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



