<template>
  <div>
    <!-- responsive view -->
    <div class="resProgress">
      <p class="resProgress__title" v-show="level == 1">
        <span class="progressFlow__indvdul-icon" v-show="!done">1</span>
        <span>Members Requirement</span>
      </p>
      <p class="resProgress__title" v-show="level == 2">
        <span class="progressFlow__indvdul-icon" v-show="!done">2</span>
        <span>Subscription Type</span>
      </p>

      <p class="resProgress__title" v-show="level == 3 && dlsValue">
        <span class="progressFlow__indvdul-icon" v-show="!done">2</span>
        <span>Subscription Type</span>
      </p>

      <p class="resProgress__title" v-show="level >= 3 && level <= 6 && !rlsValue && !dlsValue">
        <span class="progressFlow__indvdul-icon" v-show="!done">3</span>
        <span>Price your Space</span>
      </p>

      <p class="resProgress__title" v-show="level >= 3 && level <= 6 && rlsValue">
        <span class="progressFlow__indvdul-icon" v-show="!done">3</span>
        <span>Price your Space</span>
      </p>

      <p class="resProgress__title" v-show="level >= 4 && level <= 6 && dlsValue">
        <span class="progressFlow__indvdul-icon" v-show="!done">3</span>
        <span>Price your Space</span>
      </p>
    </div>

    <ul class="progressFlow">
      <li
        class="progressFlow__indvdul stepTwo"
        v-bind:class="{ done: level>1, active: level >= 1 }"
      >
        <span class="progressFlow__indvdul-icon progess__numbering">1</span>
        <span class="progressFlow__indvdul-icon progess__icon">
          <svgicon icon="verified" class="progressFlow__icon"></svgicon>
        </span>
        <span>Members Requirement</span>
      </li>

      <li
        class="progressFlow__indvdul stepTwo"
        v-bind:class="{ done: level>3, active: level >= 2 }"
        v-show="dlsValue"
      >
        <span class="progressFlow__indvdul-icon progess__numbering">2</span>
        <span class="progressFlow__indvdul-icon progess__icon">
          <svgicon icon="verified" class="progressFlow__icon"></svgicon>
        </span>
        <span>Subscription Type</span>
      </li>

      <li
        class="progressFlow__indvdul stepTwo"
        v-bind:class="{ done: level>2, active: level >= 2 }"
        v-show="rlsValue || !dlsValue"
      >
        <span class="progressFlow__indvdul-icon progess__numbering">2</span>
        <span class="progressFlow__indvdul-icon progess__icon">
          <svgicon icon="verified" class="progressFlow__icon"></svgicon>
        </span>
        <span>Subscription Type</span>
      </li>

      <li
        class="progressFlow__indvdul stepTwo"
        v-bind:class="{ done: level>6 , active: level >= 3 }"
        v-if="!rlsValue && !dlsValue"
      >
        <span class="progressFlow__indvdul-icon progess__numbering">3</span>
        <span class="progressFlow__indvdul-icon progess__icon">
          <svgicon icon="verified" class="progressFlow__icon"></svgicon>
        </span>
        <span>Price your Space</span>
      </li>

      <li
        class="progressFlow__indvdul stepTwo"
        v-bind:class="{ done: level>6 , active: level >= 3 }"
        v-if="rlsValue"
      >
        <span class="progressFlow__indvdul-icon progess__numbering">3</span>
        <span class="progressFlow__indvdul-icon progess__icon">
          <svgicon icon="verified" class="progressFlow__icon"></svgicon>
        </span>
        <span>Price your Space</span>
      </li>

      <li
        class="progressFlow__indvdul stepTwo"
        v-bind:class="{ done: level>6 , active: level >= 4 }"
        v-if="dlsValue"
      >
        <span class="progressFlow__indvdul-icon progess__numbering">3</span>
        <span class="progressFlow__indvdul-icon progess__icon">
          <svgicon icon="verified" class="progressFlow__icon"></svgicon>
        </span>
        <span>Price your Space</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "listingProgress",
  data() {
    return {
      done: true,
      rlsValue: false,
      dlsValue: false
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    subTypeCheck() {
      if (this.subType.includes("monthly")) {
        this.rlsValue = true;
        this.dlsValue = false;
      } else if (this.subType.includes("daily")) {
        this.dlsValue = true;
        this.rlsValue = false;
      }
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    },
    subType() {
      return this.$store.state.subType;
    }
  },
  updated() {
    this.subTypeCheck();
  },
  mounted() {
    this.subTypeCheck();
  }
};
</script>


