<template>
  <div class="prop" v-show="level == 1">
    <form action @submit.prevent="handleSubmit">
      <div class="listing__wrap">
        <h3 class="listing__title">What spaces are you listing</h3>

        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0">
            <div class="form-group">
              <label for class="form__label">First, lets narrow things down</label>
              <div class="mb-2" v-if="submitted && $v.narrowValue.$error">
                <div class="error" v-if="!$v.narrowValue.required">This field is required</div>
              </div>
              <div class="form__select">
                <multiselect
                  v-model="narrowValue"
                  :options="narrowOptions"
                  @select="onNarrowSelect"
                ></multiselect>
              </div>
            </div>
          </div>
        </div>

        <div class="row mx-0 form__row">
          <div class="col-lg-12 px-0">
            <div class="form-group">
              <label for class="form__label">Now choose a property type</label>
              <div class="mb-2" v-if="submitted && $v.propTypeValue.$error">
                <div
                  class="error"
                  v-if="!$v.propTypeValue.required"
                >You haven't selected a property type</div>
              </div>
              <div class="row mx-0">
                <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
                  <div class="form__select">
                    <multiselect
                      v-model="propTypeValue"
                      :options="propTypeOptions"
                      @select="onPropTypeSelect"
                    ></multiselect>
                  </div>
                </div>
                <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4">
                  <p class="form__input-info">{{ propTypeInfo }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- Co-shared Space, Studio Space or Entire Unit? -->
        <div class="row mx-0">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0">
            <p class="form__section-title">Co-shared Space, Studio Space or Entire Unit?</p>
          </div>
        </div>

        <div class="mb-2" v-if="submitted && $v.spaceType.$error">
          <div class="error" v-if="!$v.spaceType.required">You haven't selected a space type</div>
        </div>

        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
            <label class="form__check-wrap">
              <label class="form__check-container">
                <input
                  type="radio"
                  name="spaceType"
                  class="form__check"
                  value="Co-sharing space"
                  v-model.lazy="spaceType"
                />
                <span class="form__check-label">Co-sharing space</span>
                <span class="form__check-checkmark"></span>
              </label>
            </label>
          </div>
          <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4">
            <p
              class="form__input-info"
            >Members sleep in their private bedroom with ensuite or adjoining bathroom and toilet, with common area and kitchen that could be shared with others.</p>
          </div>
        </div>
        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
            <label class="form__check-wrap">
              <label class="form__check-container">
                <input
                  type="radio"
                  name="spaceType"
                  class="form__check"
                  value="Studio or Self Contained Space"
                  v-model.lazy="spaceType"
                />
                <span class="form__check-label">Studio or Self Contained Space</span>
                <span class="form__check-checkmark"></span>
              </label>
            </label>
          </div>
          <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4">
            <p
              class="form__input-info"
            >Member has sole access to a private room, bathroom, toilet and kitchen/kitchenette.</p>
          </div>
        </div>
        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
            <label class="form__check-wrap">
              <label class="form__check-container">
                <input
                  type="radio"
                  name="spaceType"
                  class="form__check"
                  value="Entire Unit"
                  v-model.lazy="spaceType"
                />
                <span class="form__check-label">Entire Unit</span>
                <span class="form__check-checkmark"></span>
              </label>
            </label>
          </div>
          <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4">
            <p
              class="form__input-info"
            >Members have the whole unit to themselves. This usually includes a bedroom, a bathroom, a living room and a kitchen.</p>
          </div>
        </div>

        <!-- Are you a live-in Landlord -->
        <div class="row mx-0">
          <div class="col-lg-6 col-md-6 col-sm-12 px-0">
            <p class="form__section-title">Are you a live-in Landlord</p>
          </div>
        </div>

        <div class="mb-2" v-if="submitted && $v.liveInLL.$error">
          <div class="error" v-if="!$v.liveInLL.required">Are you a live-in Landlord</div>
        </div>
        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
            <label class="form__check-wrap">
              <label class="form__check-container">
                <input
                  type="radio"
                  name="live-inLL"
                  class="form__check"
                  value="No, it’s primarily set up for Members"
                  v-model="liveInLL"
                />
                <span class="form__check-label">No, it’s primarily set up for Members</span>
                <span class="form__check-checkmark"></span>
              </label>
            </label>
          </div>
        </div>
        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
            <label class="form__check-wrap">
              <label class="form__check-container">
                <input
                  type="radio"
                  name="live-inLL"
                  class="form__check"
                  value="Yes, I keep my personal belongings here or Live-in"
                  v-model="liveInLL"
                />
                <span class="form__check-label">Yes, I keep my personal belongings here or Live-in</span>
                <span class="form__check-checkmark"></span>
              </label>
            </label>
          </div>
        </div>

        <!-- Are you listing on SPLEET as part of a company? -->
        <div class="row mx-0">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0">
            <p class="form__section-title">Are you listing on SPLEET as part of a company?</p>
          </div>
          <div class="col-lg-7 col-md-6 col-sm-12 px-0 px-sm-4 mb-2">
            <p
              class="form__input-info"
            >This helps you get the right features for how you host—it won’t show up to members or impact how you show up in search.</p>
          </div>
        </div>
        <div class="mb-2" v-if="submitted && $v.aCompany.$error">
          <div class="error" v-if="!$v.aCompany.required">Select if you're listing as a company</div>
        </div>
        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
            <label class="form__check-wrap">
              <label class="form__check-container">
                <input
                  type="radio"
                  name="aCompany"
                  class="form__check"
                  value="No, that doesn’t sound like me"
                  v-model="aCompany"
                />
                <span class="form__check-label">No, that doesn’t sound like me</span>
                <span class="form__check-checkmark"></span>
              </label>
            </label>
          </div>
        </div>
        <div class="row mx-0 form__row">
          <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
            <label class="form__check-wrap">
              <label class="form__check-container">
                <input
                  type="radio"
                  name="aCompany"
                  class="form__check"
                  value="Yes, I work for or run a hospitality business"
                  v-model="aCompany"
                />
                <span class="form__check-label">Yes, I work for or run a hospitality business</span>
                <span class="form__check-checkmark"></span>
              </label>
            </label>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="submit" class="step__next cta__btn">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import { required } from "vuelidate/lib/validators";

export default {
  name: "propType",
  components: {
    Multiselect
  },
  data() {
    return {
      narrowValue: null,
      narrowOptions: ["Apartments", "House", "Secondary unit"],
      propTypeValue: null,
      propTypeOptions: [],
      propTypeInfo: "",
      liveInLL: "",
      aCompany: "",
      submitted: false
    };
  },
  validations: {
    spaceType: {
      required
    },
    narrowValue: {
      required
    },
    propTypeValue: {
      required
    },
    liveInLL: {
      required
    },
    aCompany: {
      required
    }
  },
  methods: {
    onNarrowSelect(option) {
      if (option === this.narrowOptions[0]) {
        this.propTypeOptions = [
          "Apartments",
          "Serviced apartments",
          "Penthouse",
          "Hostel"
        ];
        this.propTypeValue = "";
      } else if (option === this.narrowOptions[1]) {
        this.propTypeOptions = ["House", "Bungalow", "Duplex", "Townhouse"];
        this.propTypeValue = "";
      } else if (option === this.narrowOptions[2]) {
        this.propTypeOptions = ["Guesthouse", "Guest Suite"];
        this.propTypeValue = "";
      } else {
        this.propTypeOptions = [];
        this.propTypeValue = "";
      }
    },

    onPropTypeSelect(option) {
      if (option === "Apartments") {
        this.propTypeInfo =
          "Apartments are typically located in multi-unit residential buildings or complexes where other people live.";
      } else if (option === "Serviced apartments") {
        this.propTypeInfo =
          "Serviced apartments are furnished and serviced by professional management companies. They offer hotel-like amenities such as daily cleaning, laundry service, concierge, and a front desk, making them popular choices for corporate housing and Members staying longer than 30 days";
      } else if (option === "Penthouse") {
        this.propTypeInfo =
          "Penthouse is an apartment or unit on the highest floor of an apartment building and are typically differentiated from other apartments by luxury features such as bigger rooms and balcony";
      } else if (option === "Hostel") {
        this.propTypeInfo =
          "Hostel is a sociable accommodation where Members can share private rooms usually a personal bunk bed, in a dormitory and share a bathroom, lounge and sometimes a kitchen.";
      } else if (option === "House") {
        this.propTypeInfo =
          "Houses are residential buildings that are often stand-alone structures. Some houses, like duplexes, may share walls or outdoor areas with other houses.";
      } else if (option === "Bungalow") {
        this.propTypeInfo =
          "Bungalows are houses with architectural features like a wide front porch and a sloping roof. They’re usually single-level homes.";
      } else if (option === "Duplex") {
        this.propTypeInfo =
          "Duplex refers to a maisonette, a single dwelling unit spread over two floors connected by an indoor staircase.";
      } else if (option === "Townhouse") {
        this.propTypeInfo =
          "Townhouse are triplex apartment that spread out over three floors.";
      } else if (option === "Guesthouse") {
        this.propTypeInfo =
          "A guesthouse is a detached building that shares a property with another stand-alone structure like a house.";
      } else if (option === "Guest suite") {
        this.propTypeInfo =
          "Guest suites are typically ensuite (with toilet and bathroom) have a private entrance and are inside of or attached to a larger structure like a house, with access to the apartment or house kitchen or occasionally with its private kitchen. These are sometimes called BQ (Boys quarters)";
      } else {
        this.propTypeInfo = "";
      }
    },

    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    handleSubmit(e) {
      this.submitted = true;

      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }

      this.incrementLevel();
    }
  },
  computed: {
    level() {
      return this.$store.getters.level;
    },
    onlyOneBed() {
      return this.$store.state.onlyOneBed;
    },
    spaceType: {
      get() {
        return this.$store.state.selectedSpaceType;
      },
      set(value) {
        this.$store.commit("updateSpaceType", value);
      }
    },
    entireSpace() {
      return this.$store.state.entireSpace;
    },
    coSharedSpace() {
      return this.$store.state.coSharedSpace;
    }
  },
  watch: {
    spaceType: function(value) {
      if (value.includes("Studio")) {
        this.$store.state.onlyOneBed = true;
        this.$store.state.entireSpace = true;
        this.$store.state.coSharedSpace = false;
      } else if (value.includes("Entire")) {
        this.$store.state.onlyOneBed = false;
        this.$store.state.entireSpace = true;
        this.$store.state.coSharedSpace = false;
      } else if (value.includes("Co-sharing")) {
        this.$store.state.entireSpace = false;
        this.$store.state.coSharedSpace = true;
        this.$store.state.onlyOneBed = false;
      }
    }
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>


