<template>
  <div class="signup">
    <div class="register__logo">
      <router-link to="/">
        <img src="../../assets/images/logo.png" alt class="img-fluid" />
      </router-link>
    </div>
    <div v-show="signup">
      <h3 class="register__title">Create account</h3>
      <form action class="signup__form" @submit.prevent="handleSubmit">
        <a href="#" class="signup__alt">
          <div class="signup__google">
            <div class="signup__wrap">
              <div class="signup__icon">
                <img src="../../../svg-icons/google.svg" alt="google logo" class="img-fluid" />
              </div>
              <div class="signup__text-wrap">
                <span class="signup__google-text">Sign up with Google</span>
              </div>
            </div>
          </div>
        </a>

        <a href="#" class="signup__alt">
          <div class="signup__fb">
            <div class="signup__wrap">
              <div class="signup__icon">
                <svgicon icon="facebook" class="signup__icon-fb"></svgicon>
              </div>
              <div class="signup__text-wrap">
                <span class="signup__fb-text">Sign up with Facebook</span>
              </div>
            </div>
          </div>
        </a>

        <div class="login__divider">
          <hr />
          <span>or</span>
          <hr />
        </div>

        <float-label class="form-group">
          <input
            type="email"
            name="email"
            placeholder="Email"
            v-model="user.email"
            class="form__input"
          />
          <div class="mt-2" v-if="submitted && $v.user.email.$error">
            <div class="error" v-if="!$v.user.email.required">Email is required</div>
            <div class="error" v-if="!$v.user.email.email">Email is invalid</div>
          </div>
        </float-label>

        <float-label class="form-group">
          <input
            type="text"
            name="first name"
            placeholder="First Name"
            class="form__input"
            v-model="user.fname"
          />
          <div class="mt-2" v-if="submitted && $v.user.fname.$error">
            <div class="error" v-if="!$v.user.fname.required">First name is required</div>
            <div
              class="error"
              v-if="!$v.user.fname.minLength"
            >First name must have at least {{$v.user.fname.$params.minLength.min}} letters.</div>
          </div>
        </float-label>

        <float-label class="form-group">
          <input
            type="text"
            name="last name"
            placeholder="Last Name"
            v-model="user.lname"
            class="form__input"
          />
          <div class="mt-2" v-if="submitted && $v.user.lname.$error">
            <div class="error" v-if="!$v.user.lname.required">Last name is required</div>
            <div
              class="error"
              v-if="!$v.user.lname.minLength"
            >Last name must have at least {{$v.user.lname.$params.minLength.min}} letters.</div>
          </div>
        </float-label>

        <float-label class="form-group">
          <input
            :type="passwordType"
            name="password"
            placeholder="Password"
            v-model="user.pword"
            class="form__input mb-0"
          />
          <div class="signup__show-pword" @click="switchVisibility">
            <svgicon :icon="showPwordIcon"></svgicon>
          </div>

          <div class="mt-2" v-if="submitted && $v.user.pword.$error">
            <div class="error" v-if="!$v.user.pword.required">Password is required</div>
            <div
              class="error"
              v-if="!$v.user.pword.minLength"
            >Password must be at least {{$v.user.pword.$params.minLength.min}} characters.</div>
          </div>
        </float-label>

        <div class="signup__terms">
          <label class="form__check-container">
            <input type="checkbox" name="terms" class="form__check" v-model="user.acceptTerms" />
            <span class="form__check-label">
              I accept to
              <a href="#">terms of service</a>,
              <a href="#">cancelation policy</a>,
              <a href="#">non-discriminatory policy</a>,
              <a href="#">payment terms of service</a> and
              <a href="#">privacy policy</a>
            </span>
            <span class="form__check-checkmark"></span>
            <div class="mt-2" v-if="submitted && $v.user.acceptTerms.$error">
              <div
                class="error"
                v-if="!$v.user.acceptTerms.required"
              >You cannot use SPLEET website without accepting the updated Terms of Service</div>
            </div>
          </label>
        </div>

        <button type="submit" class="cta__btn">Get Started</button>
        <p class="signup__login">
          Already have a SPLEET account?
          <router-link to="/register" class="signup__login-link">Login</router-link>
        </p>
      </form>
    </div>

    <div v-show="!signup">
      <h3 class="register__title">Verify your Email Address</h3>
      <p class="signup__verify">
        Before proceeding, please check your email for a verification link.
        If you did not receive the email, Click
        <a
          href="#"
        >here</a> to request another
      </p>
    </div>
  </div>
</template>

<script>
import { required, minLength, email, sameAs } from "vuelidate/lib/validators";
import { signup } from "../../../services/user";
import routes from "../../routes";

export default {
  name: "signup",
  data() {
    return {
      signup: true,
      user: {
        email: "",
        fname: "",
        lname: "",
        pword: "",
        acceptTerms: false
      },
      passwordType: "password",
      showPwordIcon: "view",
      submitted: false
    };
  },
  validations: {
    user: {
      email: {
        required,
        email
      },
      fname: {
        required,
        minLength: minLength(2)
      },
      lname: {
        required,
        minLength: minLength(2)
      },
      pword: {
        required,
        minLength: minLength(6)
      },
      acceptTerms: {
        sameAs: sameAs(() => true)
      }
    }
  },
  methods: {
    switchVisibility() {
      this.passwordType =
        this.passwordType === "password" ? "text" : "password";
      this.showPwordIcon = this.showPwordIcon === "view" ? "hide" : "view";
    },
    async register() {
      await signup(
        this.user.email,
        this.user.fname,
        this.user.lname,
        this.user.pword
      ).then(
        user => {
          // commit("loginSuccess", userInfo);
          localStorage.setItem("user", JSON.stringify(user));
          this.$router.push("/register");
          if (user) {
            this.userCheck = true;
          } else {
            this.userCheck = false;
          }
        },
        error => {
          // commit("loginFailure", error);
          // dispatch("alert/error", error, { root: true });
          this.loginFail = error.error;
        }
      );
    },
    submitSuccess() {
      this.signup = false;
      // alert("SUCCESS!! :-)\n\n" + JSON.stringify(this.user));
      this.register();
    },
    handleSubmit(e) {
      this.submitted = true;

      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }

      this.submitSuccess();
    }
  }
};
</script>


