<template>
  <div class="prop" v-if="level == 4 && rlsValue && (quarterly || biannually)">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Something special for your Members</h3>
        <p
          class="form__input-info picture__subtitle"
        >Encourage home seekers to book longer stays by offering a discount.</p>

        <div class="memRequire">
          <label for class="form__label form__row">Rolling Living Subscription (RLS)</label>

          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <div class="form__row" v-show="quarterly">
                <label for class="form__label">Quarterly Discount (%)</label>
                <div class="form__select">
                  <multiselect v-model="quarterlyDiscount" :options="discountOptions"></multiselect>
                </div>
              </div>

              <div v-show="biannually">
                <label for class="form__label">Biannually Discount (%)</label>
                <div class="form__select">
                  <multiselect v-model="biannuallyDiscount" :options="discountOptions"></multiselect>
                </div>
              </div>
            </div>

            <div class="col-lg-7 col-md-6 col-sm-12">
              <div class="d-flex">
                <p class="tip">Tip:</p>
                <p
                  class="tip__info"
                >Your discount helps make your listing attractive to home seekers searching for month-long stays.</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="step__next cta__btn" @click="nextButton">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";

export default {
  name: "rlsDiscount",
  components: {
    Multiselect
  },

  data() {
    return {
      rlsValue: false,
      dlsValue: false,
      quarterlyDiscount: "",
      discountOptions: [0, 5, 7.5, 10],
      biannuallyDiscount: ""
    };
  },

  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    spaceTypeCheck() {
      if (this.spaceType.includes("Studio")) {
        this.$store.state.entireSpace = true;
        this.$store.state.coSharedSpace = false;
      } else if (this.spaceType.includes("Entire")) {
        this.$store.state.entireSpace = true;
        this.$store.state.coSharedSpace = false;
      } else if (this.spaceType.includes("Co-sharing")) {
        this.$store.state.entireSpace = false;
        this.$store.state.coSharedSpace = true;
      }
    },
    subTypeCheck() {
      this.spaceTypeCheck();
      if (this.subType.includes("monthly")) {
        this.rlsValue = true;
        this.dlsValue = false;
      } else if (this.subType.includes("daily")) {
        this.dlsValue = true;
        this.rlsValue = false;
      }
    },
    nextButton() {
      return (this.$store.state.level = 6);
    }
  },

  computed: {
    quarterly() {
      return this.$store.state.quarterly;
    },
    biannually() {
      return this.$store.state.biannually;
    },
    level() {
      return this.$store.state.level;
    },
    subType() {
      return this.$store.state.subType;
    },
    spaceType() {
      return this.$store.state.selectedSpaceType;
    }
  },
  updated() {
    this.subTypeCheck();
  },
  mounted() {
    this.subTypeCheck();
  }
};
</script>


