<template>
  <div class="prop" v-if="level == 3 && dlsValue">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Update your calendar</h3>
        <p
          class="form__input-info picture__subtitle"
        >Cancelling disrupts Members’ plans. If you cancel because your calendar is inaccurate, you’ll be charged a penalty fee and the dates won’t be available for anyone else to book</p>

        <div class="memRequire">
          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label class="form__check-wrap">
                <label class="form__check-container">
                  <input
                    type="checkbox"
                    name="keepCalendarUpdated"
                    class="form__check"
                    v-model.lazy="keepCalendarUpdated"
                  />
                  <span class="form__check-label">Got it! I’ll keep my calendar up to date</span>
                  <span class="form__check-checkmark"></span>
                </label>
              </label>
            </div>
          </div>

          <div class="row mx-0 form__row">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label">How long can members stay?</label>
              <div class="d-flex mb-3">
                <input name="minNight" type="text" class="form__input" :value="minNightValue" />
                <button
                  type="button"
                  :disabled="minNight<=1"
                  class="calendar__btn"
                  :class="{disableBtn : minNight<=1}"
                  @click="decreaseMinNight"
                >-</button>
                <button type="button" class="calendar__btn" @click="increaseMinNight">+</button>
              </div>

              <div class="d-flex">
                <input name="maxNight" type="text" class="form__input" :value="maxNightValue" />
                <button
                  type="button"
                  class="calendar__btn"
                  @click="decreaseMaxNight"
                  :class="{disableBtn : maxNight<=1 || maxNight <= minNight}"
                  :disabled="maxNight<=1 || maxNight <= minNight"
                >-</button>
                <button type="button" class="calendar__btn" @click="increaseMaxNight">+</button>
              </div>
            </div>

            <div class="col-lg-7 col-md-6 col-sm-12">
              <div class="d-flex">
                <p class="tip">Tip:</p>
                <p
                  class="tip__info"
                >Shorter trips can create more bookings, but you might have to turn over your space more often</p>
              </div>
            </div>
          </div>

          <div class="row mx-0 mb-2">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label">How far in advance can Member book?</label>
              <div class="form__select form__row">
                <multiselect v-model="advanceBook" :options="advanceBookOptions"></multiselect>
              </div>
              <div class="d-flex flex-wrap">
                <div
                  class="advn__graph-wrap mr-4 mb-3"
                  v-for="(advanceDatas, index) in advanceData"
                  :key="index"
                >
                  <span class="advn__graph-hang"></span>
                  <span class="advn__graph-hang second-hang"></span>
                  <div class="advn__graph">
                    <div class="advn__graph-top"></div>
                    <div class="advn__graph-text-wrap">
                      <p class="advn__graph-text">{{advanceDatas}}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-lg-7 col-md-6 col-sm-12">
              <div class="d-flex">
                <p class="tip">Tip:</p>
                <p class="tip__info">Most hosts can keep their calendars updated up to 6 months out.</p>
              </div>
            </div>
          </div>

          <div class="row mx-0 mb-2">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label">How much notice do you need before a Members arrives?</label>
              <div class="form__select form__row">
                <multiselect v-model="arrivalNotice" :options="arrivalNoticeOptions"></multiselect>
              </div>
            </div>

            <div class="col-lg-7 col-md-6 col-sm-12">
              <div class="d-flex">
                <p class="tip">Tip:</p>
                <p
                  class="tip__info"
                >At least 1 day’s notice can help you plan for a Member’s arrival, but same-day might help you not to miss out on last-minute trips.</p>
              </div>
            </div>
          </div>

          <div class="row mx-0 mb-2">
            <div class="col-lg-5 col-md-6 col-sm-12 px-0 mb-2">
              <label for class="form__label">What is your preferred check-in time?</label>
              <div class="d-flex">
                <div class="mr-auto checkin__wrap">
                  <label for class="form__label">From</label>
                  <div class="form__select form__row">
                    <multiselect v-model="checkinFrom" :options="checkinOptions"></multiselect>
                  </div>
                </div>

                <div class="checkin__wrap">
                  <label for class="form__label">To</label>
                  <div class="form__select form__row">
                    <multiselect v-model="checkinTo" :options="checkinOptions"></multiselect>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="step__next cta__btn" @click="incrementLevel">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import moment from "moment";

export default {
  name: "updateCalendar",
  components: {
    Multiselect
  },
  data() {
    return {
      rlsValue: false,
      dlsValue: false,
      keepCalendarUpdated: "",
      minNight: 1,
      maxNight: 1,
      advanceBook: "",
      advanceBookOptions: [
        "Anytime",
        "3 months",
        "6 months",
        "9 months",
        "1 year"
      ],
      advanceData: [],
      arrivalNotice: "",
      arrivalNoticeOptions: ["Same day", "1 day", "2 days", "3 days", "7 days"],
      checkinTo: "",
      checkinFrom: "",
      checkinOptions: [
        "Flexible",
        "7AM",
        "8AM",
        "9AM",
        "10AM",
        "11AM",
        "12PM(noon)",
        "1PM",
        "2PM",
        "3PM",
        "4PM",
        "5PM",
        "6PM",
        "7PM",
        "8PM",
        "9PM",
        "10PM",
        "11PM",
        "12AM(midnight)"
      ]
    };
  },
  methods: {
    subTypeCheck() {
      if (this.subType.includes("monthly")) {
        this.rlsValue = true;
        this.dlsValue = false;
      } else if (this.subType.includes("daily")) {
        this.dlsValue = true;
        this.rlsValue = false;
      }
    },
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    increaseMinNight() {
      this.minNight++;
      this.maxNight = this.minNight;
    },
    decreaseMinNight() {
      this.minNight--;
    },
    increaseMaxNight() {
      this.maxNight++;
    },
    decreaseMaxNight() {
      this.maxNight--;
    },
    checkMinMaxNight() {
      if (this.minNight > this.maxNight) {
        this.maxNight = this.minNight;
      }
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    },
    subType() {
      return this.$store.state.subType;
    },
    minNightValue() {
      return this.minNight + "  night(s) min";
    },
    maxNightValue() {
      return this.maxNight + "  night(s) max";
    }
  },
  watch: {
    advanceBook(value) {
      const currentDate = new Date();
      const monthNames = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec"
      ];
      if (value.includes("Anytime")) {
        this.advanceData = [];
        this.advanceData.push(
          currentDate.getFullYear() + 1,
          currentDate.getFullYear() + 2,
          currentDate.getFullYear() + 3
        );
      } else if (value.includes("3 months")) {
        this.advanceData = [];
        this.advanceData.push(
          monthNames[
            moment()
              .add(1, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(2, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(3, "M")
              .month()
          ]
        );
      } else if (value.includes("6 months")) {
        this.advanceData = [];
        this.advanceData.push(
          monthNames[
            moment()
              .add(1, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(2, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(3, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(4, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(5, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(6, "M")
              .month()
          ]
        );
      } else if (value.includes("9 months")) {
        this.advanceData = [];
        this.advanceData.push(
          monthNames[
            moment()
              .add(1, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(2, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(3, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(4, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(5, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(6, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(7, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(8, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(9, "M")
              .month()
          ]
        );
      } else if (value.includes("1 year")) {
        this.advanceData = [];
        this.advanceData.push(
          monthNames[
            moment()
              .add(1, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(2, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(3, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(4, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(5, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(6, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(7, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(8, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(9, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(10, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(11, "M")
              .month()
          ],
          monthNames[
            moment()
              .add(12, "M")
              .month()
          ]
        );
      }
    }
  },
  updated() {
    this.subTypeCheck();
    this.checkMinMaxNight();
  },
  mounted() {
    this.subTypeCheck();
  }
};
</script>



