<template>
  <div>
    <appListing></appListing>
    <div class="page-container">
      <appListingProgress></appListingProgress>
      <appPersonlInfo></appPersonlInfo>
      <appCompanyDetails></appCompanyDetails>
      <appNextOfKin></appNextOfKin>
      <appIdentification></appIdentification>
      <appPayment></appPayment>
    </div>
  </div>
</template>

<script>
import ListingNav from "./components/stepThree/listingNavbar";
import listingProgress from "./components/stepThree/listingProgress";
import personlInfo from "./components/stepThree/personalInfo";
import companyDetails from "./components/stepThree/companyDetails";
import nextOfKin from "./components/stepThree/nextOfKin";
import identification from "./components/stepThree/identification";
import payment from "./components/stepThree/payment";

export default {
  name: "stepThree",
  components: {
    appListing: ListingNav,
    appListingProgress: listingProgress,
    appPersonlInfo: personlInfo,
    appCompanyDetails: companyDetails,
    appNextOfKin: nextOfKin,
    appIdentification: identification,
    appPayment: payment
  },
  data() {
    return {};
  }
};
</script>

<style>
#app {
  font-family: "Sailec", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(5, 6, 9, 0.7);
  background: #f5f5f5;
  position: relative;
  height: 100vh;
}
</style>


