<template>
  <div v-show="level==6">
    <form action>
      <div class="listing__wrap">
        <div class="description">
          <form action>
            <div class="form-group description__wrap">
              <h3 class="listing__title mb-2">Name your place</h3>
              <label
                for="description"
                class="form__input-info"
              >Attract guests with a listing title that highlights what makes your place special.</label>
              <textarea
                maxlength="200"
                name="description"
                class="description__textarea-name form__textarea"
                v-on:keyup="countdown"
                v-model="spaceName"
              ></textarea>
              <small class="form__textarea-count">{{spaceNameCount}}</small>
            </div>
          </form>
        </div>
      </div>
      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button @click="incrementLevel" type="button" class="step__next cta__btn">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "spaceName",
  data() {
    return {
      maxCount: 200,
      spaceNameCount: 200,
      spaceName: ""
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    countdown: function() {
      this.spaceNameCount = this.maxCount - this.spaceName.length;
    }
  },
  computed: {
    level() {
      return this.$store.getters.level;
    }
  }
};
</script>


