<template>
  <div v-show="level>=7">
    <form action>
      <div class="listing__wrap">
        <div class="location">
          <location-frame>
            <form
              action
              slot-scope="{
            address,
            error,
            fetchAddress,
            geolocationSupported,
            loading,
            streetName,
            populate,
            locality,
            state,
            country,
            suggestions,
            city,
            neigbourhood,
            coordinatesL,
            mapShow

          }"
            >
              <div v-show="!mapShow">
                <h3 class="listing__title mb-2">Where’s your place located?</h3>
                <p
                  class="form__input-info"
                >Guests will only get your exact address once they’ve booked a reservation.</p>
              </div>

              <div v-show="mapShow">
                <h3 class="listing__title mb-2">Is the pin in the right place?</h3>
                <p
                  class="form__input-info"
                >If needed, you can adjust the map so the pin is in the right location. Only confirmed guests will see this, so they know how to get to your place.</p>
                <div id="map" class="location__map"></div>
              </div>

              <button
                class="location__btn"
                v-if="geolocationSupported"
                type="button"
                @click="fetchAddress"
              >
                <svgicon icon="location" class="location__btn-icon"></svgicon>
                <span>{{ loading ? `Loading...` : `Use current location` }}</span>
              </button>
              <div
                v-if="error"
                class="error"
              >There was an error fetching your location, please try again.</div>

              <div v-show="suggestions">
                <h3
                  class="form__label"
                >Click on the address that is yours or close to yours and edit the form to the correct address</h3>
                <ul class="location__suggestion-wrap">
                  <li
                    v-for="(addresses, index) in address"
                    :key="index"
                    @click="populate(index)"
                    class="location__suggestion"
                  >{{addresses.formatted_address}}</li>
                </ul>
              </div>

              <div class="location__form">
                <div class="form-group form__row">
                  <label for="address" class="form__label">Street Address</label>
                  <textarea name="address" class="form__textarea mt-0" :value="streetName"></textarea>
                </div>

                <div class="form-group form__row">
                  <label for="landmark" class="form__label">Landmark</label>
                  <input :value="neigbourhood" name="landmark" type="text" class="form__input" />
                </div>

                <div class="form-group form__row">
                  <label for="country" class="form__label">Country/Region</label>
                  <div class="form__select">
                    <multiselect
                      :value="country"
                      :options="countryOptions"
                      placeholder="Select Country"
                    ></multiselect>
                  </div>
                </div>

                <div class="row mx-0">
                  <div class="col-lg-6 col-md-6 col-sm-12 px-0 pr-md-3">
                    <div class="form-group form__row">
                      <label for="city" class="form__label">City</label>
                      <input :value="city" name="city" type="text" class="form__input" />
                    </div>
                  </div>
                  <div class="col-lg-6 col-md-6 col-sm-12 p-0">
                    <div class="form-group form__row">
                      <label for="state" class="form__label">State</label>
                      <input :value="state" name="state" type="text" class="form__input" />
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </location-frame>
        </div>
      </div>
      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="cta__btn step__next" @click="submit">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import LocationFrame from "./LocationFrame";
import routes from "../../routes";

export default {
  name: "location",
  components: {
    Multiselect,
    LocationFrame
  },
  data() {
    return {
      countryOptions: ["America", "Nigeria", "London"]
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    incrementStep() {
      this.$store.dispatch("incrementAction", 1);
    },
    submit() {
      this.incrementStep();
      this.$router.push("/breakdown");
    }
  },
  computed: {
    level() {
      return this.$store.getters.level;
    },
    step() {
      return this.$store.getters.step;
    }
  }
};
</script>



<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



