<template>
  <div class="page-container">
    <appNavbar></appNavbar>

    <div class="status__wrap">
      <div class="status__wrap-head">
        <h3 class="status__heading">Hi, {{userName}}! Let’s get started listing your space.</h3>
        <p class="status__heading-sub" v-show="step == 7"></p>
      </div>

      <div class="status__block pt-0">
        <h3 class="status__block-head">step 1</h3>
        <p class="status__block-title" v-show="step == 1">Start with the basics</p>
        <p class="status__block-summary">Bedroom, amenities, pictures, and more.</p>
        <router-link to="/Listing" class="status__block-link" v-show="step > 1">Change</router-link>
        <button class="cta__btn" type="button" v-show="step == 1" @click="stepOne">Continue</button>
        <svgicon icon="completed" class="status__block-icon" v-show="step > 1"></svgicon>
      </div>

      <div class="status__block">
        <div class="status__block-overlay" v-show="step < 2"></div>
        <h3 class="status__block-head">step 2</h3>
        <p class="status__block-title" v-show="step<=2">Get ready for Guest</p>
        <p class="status__block-summary">Booking setting, calendar, price.</p>
        <router-link to="/step-two" class="status__block-link" v-show="step>2">Change</router-link>
        <button class="cta__btn" type="button" v-show="step == 2" @click="stepTwo">Continue</button>
        <svgicon icon="completed" class="status__block-icon" v-show="step>2"></svgicon>
      </div>

      <div class="status__block">
        <div class="status__block-overlay" v-show="step<3"></div>
        <h3 class="status__block-head">step 3</h3>
        <p class="status__block-title" v-show="step<=3">We would like to know you</p>
        <p class="status__block-summary">KYC and payments</p>
        <button class="cta__btn" type="button" v-show="step == 3" @click="stepThree">Continue</button>
        <svgicon icon="completed" class="status__block-icon" v-show="step>3"></svgicon>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "./components/navbar-components/navbar";
import routes from "./routes";

export default {
  name: "stepBreakdown",
  components: {
    appNavbar: Navbar
  },
  data() {
    return {
      userName: ""
    };
  },
  methods: {
    stepOne() {
      this.$store.state.level = 1;
      this.$router.push("/listing");
    },
    stepTwo() {
      this.$store.state.level = 1;
      this.$router.push("/step-two");
    },
    stepThree() {
      this.$store.state.level = 1;
      this.$router.push("/step-three");
    }
  },
  computed: {
    step() {
      return this.$store.state.step;
    },
    level() {
      return this.$store.state.level;
    }
  },
  created: function() {
    let user = JSON.parse(localStorage.getItem("user"));
    this.userName = user.user.first_name;
  }
};
</script>


