<template>
  <div class="prop" v-show="level == 4">
    <form action>
      <div class="listing__wrap">
        <h3 class="listing__title mb-2">Identification/Photo ID</h3>
        <p
          class="form__input-info picture__subtitle"
        >After adding your selected National ID, we will check to see if the photo of your ID matches the new photo you’ll take of youself</p>

        <div class="memRequire">
          <div class="d-flex identification__wrap">
            <div class="form__upload-wrap identification__upload-wrap">
              <p class="form__label text-center">Upload your ID</p>
              <div class="form__upload" :style="{backgroundImage: 'url('+previewID+')'}">
                <div class="identification__upload-placeholder-wrap">
                  <svgicon
                    icon="image-placeholder"
                    class="identification__upload-placeholder"
                    v-show="!previewSuccess"
                  ></svgicon>
                </div>
              </div>
              <div class="text-center form__section">
                <div class="identification__upload-wrap">
                  <input type="file" class="identification__upload" @change="uploadID" />
                  <button
                    class="cta__btn"
                    type="button"
                  >{{previewSuccess ? "Reupload ID" : "Upload ID"}}</button>
                </div>
              </div>

              <div>
                <label for class="form__label">Select what type of document you uploaded</label>
                <div class="form__select">
                  <multiselect v-model="documentType" :options="documentTypeOptions"></multiselect>
                </div>
              </div>
            </div>

            <hr class="identification__divider" />

            <div class="form__upload-wrap camera__upload">
              <p class="form__label text-center">Take a selfie of yourself</p>
              <div class="form__upload" :style="{backgroundImage: 'url('+img+')'}">
                <div class="identification__upload-placeholder-wrap">
                  <svgicon
                    v-show="!imgUpload"
                    icon="camera"
                    class="identification__upload-placeholder"
                  ></svgicon>
                </div>
              </div>
              <div class="text-center form__section">
                <button
                  class="cta__btn"
                  type="button"
                  @click="cameraModalShow"
                >{{imgUpload ? "Retake a selfie" : "Take a selfie"}}</button>
                <div class="identification__upload-wrap"></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button type="button" class="step__next cta__btn" @click="incrementLevel">Continue</button>
        </div>
      </div>

      <div class="identification__modal">
        <cameraModal :img="img" @updateSnappedImg="updateImage" @updateImgUpload="updateSuccess"></cameraModal>
      </div>
    </form>
  </div>
</template>

<script>
import Multiselect from "vue-multiselect";

import cameraModal from "./cameraModal";

export default {
  name: "identification",
  components: {
    Multiselect,
    cameraModal
  },
  data() {
    return {
      documentType: "",
      documentTypeOptions: [
        "National ID card",
        "Driver’s License",
        "International ID",
        "Voter's Card"
      ],
      img: null,
      imgUpload: false,
      previewID: null,
      previewSuccess: false
    };
  },
  methods: {
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    cameraModalShow() {
      this.$modal.show("cameraModal");
    },
    updateImage(value) {
      this.img = value;
    },
    updateSuccess(value) {
      this.imgUpload = value;
    },
    uploadID(e) {
      const image = e.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(image);
      reader.onload = e => {
        this.previewID = e.target.result;
        this.previewSuccess = true;
      };
    }
  },
  computed: {
    level() {
      return this.$store.state.level;
    }
  }
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>


