<template>
  <div v-show="level==4">
    <form action>
      <div class="listing__wrap">
        <div class="picture__top">
          <h3 class="listing__title mb-2">Add photos to your listing</h3>
          <span class="picture__tips" @click="pictureTipsShow">
            <svgicon icon="tips" class="picture__tips-icon"></svgicon>Photo tip
          </span>
        </div>

        <p
          class="form__input-info picture__subtitle"
        >Photos help members imagine staying in your place. You can start with one and add more after you publish.</p>
        <p class="error" v-show="errorMsg">You haven't added any image</p>
        <div>
          <form enctype="multipart/form-data" novalidate v-if="isInitial || isSaving">
            <div class="dropbox picture__upload">
              <input
                type="file"
                multiple
                :name="uploadFieldName"
                :disabled="isSaving"
                @change="filesChange($event.target.name, $event.target.files); fileCount = $event.target.files.length"
                accept="image/*"
                class="input-file"
              />
              <div v-if="isInitial">
                <p class="picture__upload-text">
                  <button class="cta__btn">Upload Images</button>
                  or drag them in
                </p>
              </div>
              <p v-if="isSaving">Uploading {{ fileCount }} files...</p>
            </div>
          </form>

          <!--SUCCESS-->
          <div v-if="isSuccess" class="picture__upload-success">
            <form enctype="multipart/form-data" novalidate>
              <label class="form__add d-block ml-auto">
                <span>+Add more</span>
                <input
                  type="file"
                  multiple
                  :name="uploadFieldName"
                  @change="addChange($event.target.name, $event.target.files); fileCount = $event.target.files.length"
                  accept="image/*"
                  class="input-file picture__more"
                />
              </label>
            </form>
            <div
              class="picture__upload-cover"
              :style="{backgroundImage: 'url('+require('../../assets/images/'+uploadedFiles[0].fileName+'')+')'}"
            >
              <div class="picture__upload-delete">
                <svgicon icon="delete" class="picture__upload-delete-icon"></svgicon>
              </div>
            </div>

            <div class="row mx-0">
              <div
                class="col-lg-4 col-md-4 col-sm-6 col-12 mb-5"
                v-for="(item, index) in uploadedFiles"
                :key="index"
              >
                <div class="picture__upload-list">
                  <div
                    class="picture__upload-list-img"
                    :style="{backgroundImage: 'url('+require('../../assets/images/'+item.fileName+'')+')'}"
                    :imageName="item.fileName"
                    :id="index"
                  >
                    <div class="picture__upload-delete" @click="deleteImage(index)">
                      <svgicon icon="delete" class="picture__upload-delete-icon"></svgicon>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <p>
              <a href="javascript:void(0)" @click="reset()">Upload again</a>
            </p>
            <!-- <ul class="list-unstyled">
          <li v-for="item in uploadedFiles">
            <img :src="item.url" class="img-responsive img-thumbnail" :alt="item.originalName" />
          </li>
            </ul>-->
          </div>
          <!--FAILED-->
          <div v-if="isFailed">
            <h2>Uploaded failed.</h2>
            <p>
              <a href="javascript:void(0)" @click="reset()">Try again</a>
            </p>
            <pre>{{ uploadError }}</pre>
          </div>
        </div>
        <div class="picture__modal">
          <PictureModal></PictureModal>
        </div>
      </div>

      <div class="step">
        <div class="step__wrap">
          <div class="step__prev">
            <button type="button" v-show="level > 1" @click="decrementLevel">
              <svgicon icon="down-arrow" class="step__prev-icon"></svgicon>
              <span>Back</span>
            </button>
          </div>
          <button @click="nextStep" type="button" class="step__next cta__btn">Continue</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import PictureModal from "./pictureTips";
import { upload } from "./pictureService.js";
import Multiselect from "vue-multiselect";

const STATUS_INITIAL = 0,
  STATUS_SAVING = 1,
  STATUS_SUCCESS = 2,
  STATUS_FAILED = 3;

export default {
  name: "pictures",
  components: {
    PictureModal,
    Multiselect
  },
  data() {
    return {
      uploadedFiles: [],
      uploadError: null,
      currentStatus: null,
      uploadFieldName: "photos",
      selectedType: [],
      imageSelect: [],
      formData: "",
      errorMsg: false
    };
  },
  methods: {
    nextStep() {
      if (this.uploadedFiles.length == 0) {
        this.errorMsg = true;
        return;
      } else {
        this.incrementLevel();
      }
    },
    incrementLevel() {
      this.$store.dispatch("incrementActionLevel", 1);
    },
    decrementLevel() {
      this.$store.dispatch("decrementActionLevel", 1);
    },
    pictureTipsShow() {
      this.$modal.show("PictureModal");
    },
    reset() {
      // reset form to initial state
      this.currentStatus = STATUS_INITIAL;
      this.uploadedFiles = [];
      this.uploadError = null;
    },
    onSelect(selectedOption, id) {
      const imageValue = {
        uploadIndex: id,
        imageName: this.uploadedFiles[id].fileName,
        imageType: selectedOption
      };
      //this.imageSelect.push(imageValue);
      var objIndex = this.imageSelect.filter(obj => obj.uploadIndex == id);

      if (objIndex.length) {
        this.imageSelect.splice(id, 1, imageValue);
        // console.log(this.imageSelect);
      } else {
        return this.imageSelect.push(imageValue);
      }
    },
    save(formData) {
      // upload data to the server
      this.currentStatus = STATUS_SAVING;

      upload(formData)
        .then(x => {
          this.uploadedFiles = [].concat(x);
          this.currentStatus = STATUS_SUCCESS;
        })
        .catch(err => {
          this.uploadError = err.response;
          this.currentStatus = STATUS_FAILED;
        });
    },
    filesChange(fieldName, fileList) {
      // handle file changes
      this.formData = new FormData();

      if (!fileList.length) return;
      // append the files to FormData
      Array.from(Array(fileList.length).keys()).map(x => {
        this.formData.append(fieldName, fileList[x], fileList[x].name);
      });

      this.save(this.formData);
    },
    addChange(fieldName, fileList) {
      // handle file changes

      if (fileList.length) {
        // append the files to FormData
        Array.from(Array(fileList.length).keys()).map(x => {
          this.formData.append(fieldName, fileList[x], fileList[x].name);
        });

        // save it
        this.save(this.formData);
      }
    },
    deleteImage(id) {
      this.uploadedFiles.splice(id, 1);
    }
  },
  computed: {
    level() {
      return this.$store.getters.level;
    },
    isInitial() {
      return this.currentStatus === STATUS_INITIAL;
    },
    isSaving() {
      return this.currentStatus === STATUS_SAVING;
    },
    isSuccess() {
      return this.currentStatus === STATUS_SUCCESS;
    },
    isFailed() {
      return this.currentStatus === STATUS_FAILED;
    }
  },
  mounted() {
    this.reset();
  }
};
</script>


